body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  border: none;
}
.vseller {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: #c3ddeb;
  text-align: center;
}
.vseller .seller-container {
  width: 90%;
}
.vseller .seller-container .seller-title {
  width: 100%;
  font-size: 25px;
  font-weight: bold;
  letter-spacing: 20px;
  padding-left: 20px;
  box-sizing: border-box;
  margin-bottom: 10px;
  color: #000;
}
.vseller .seller-container .seller-text {
  width: 100%;
  font-size: 14px;
  padding: 0 0 10px 6px;
  letter-spacing: 6px;
  box-sizing: border-box;
  margin-bottom: 50px;
  color: #666;
  border-bottom: 1px solid #fff;
}
.vseller .seller-container .seller-search {
  display: flex;
  justify-content: center;
  align-items: center;
}
.vseller .seller-container .seller-search .seller-getid {
  height: 30px;
  width: 200px;
  padding: 10px 35px;
  box-sizing: border-box;
  letter-spacing: 3px;
  text-align: center;
  outline: none;
  border-radius: 50px;
  color: #888;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
.vseller .seller-container .seller-search button::after {
  border: none;
}
.vseller .seller-container .seller-search button {
  margin: 0;
  padding: 0;
  background-color: rgba(0, 0, 0, 0);
}
.vseller .seller-container .seller-search .seller-search-btn {
  width: 30px;
  height: 30px;
  position: absolute;
  margin-left: 80px;
}
.vseller .seller-container .seller-search .seller-search-btn img {
  width: 30px;
}
.vseller .seller-container .seller-search ::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #aaa;
}
.vseller .seller-container [class*="cloud"] {
  position: fixed;
  bottom: 100px;
  background-color: #fff;
  border-radius: 50%;
}
.vseller .seller-container .cloud1 {
  width: 150px;
  height: 150px;
  left: -30px;
  top: 70vh;
}
.vseller .seller-container .cloud2 {
  width: 250px;
  height: 300px;
  left: 33px;
  top: 65vh;
}
.vseller .seller-container .cloud3 {
  width: 200px;
  height: 200px;
  left: 200px;
  top: 70vh;
}

.header-new {
  height: 15vh;
  background: #b2d3e2;
}
.header-new .avatar {
  border: 3px solid #fff;
  margin: 8vw;
}
.header-new .seller-name {
  display: inline;
  color: #000;
  font-weight: 700;
  letter-spacing: 5px;
}
.ant-carousel .slick-slide {
  text-align: center;
  height: 160px;
  line-height: 160px;
  background: #364d79;
  overflow: hidden;
}
.ant-carousel .slick-slide h3 {
  color: #fff;
}
.vnavbar-container {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 20px 40px 20px 40px;
  box-sizing: border-box;
  background-color: #fff;
  border-bottom: 1px lightgray solid;
}
.vnavbar-container .vnavbar-list {
  flex: 1 1;
  list-style: none;
  text-align: center;
}
.vnavbar-container .vnavbar-list a {
  display: block;
  text-align: center;
  text-decoration: none;
  font-size: 22px;
  letter-spacing: 10px;
  padding-left: 10px;
  box-sizing: border-box;
  color: #888;
  width: 50%;
  margin: 0 auto;
}
.vnavbar-container .vnavbar-list .active {
  border-bottom: 6px solid #b2d3e2;
  color: black;
  font-weight: bold;
  /* transition: all 0.3s; */
}

.vproductShow-card-container,
.vproductShow-card {
  margin: 0;
  padding: 0;
  list-style: none;
}

.vproductShow-card-container,
.vproductShow-card {
  margin: 0;
  padding: 0;
  list-style: none;
}

