// 店铺头
.header-new{
    height: 15vh;
    background: #b2d3e2;
    .avatar{
        border: 3px solid #fff;
        margin: 8vw;
    }
    .seller-name{
        display: inline;
        color: #000;
        font-weight: 700;
        letter-spacing: 5px;
    }
}
// 轮播图
.ant-carousel .slick-slide {
    text-align: center;
    height: 160px;
    line-height: 160px;
    background: #364d79;
    overflow: hidden;
  }

  .ant-carousel .slick-slide h3 {
    color: #fff;
  }
.vnavbar-container {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 20px 40px 20px 40px;
    box-sizing: border-box;
    background-color: #fff;
    border-bottom: 1px lightgray solid;
    .vnavbar-list {
        flex: 1;
        list-style: none;
        text-align: center;
        a {
            display: block;
            text-align: center;
            text-decoration: none;
            font-size: 22px;
            letter-spacing: 10px;
            padding-left: 10px;
            box-sizing: border-box;
            color: #888;
            width: 50%;
            margin: 0 auto;
        }
        .active {
            border-bottom: 6px solid #b2d3e2;
            color: black;
            font-weight: bold;
            /* transition: all 0.3s; */
        }
    }
}